#experience {
	background-color: #f5f5f5;
}

#experience-container {
	padding-top: 5.2%;
	padding-left: 7.2%;
	padding-right: 7.2%;
	padding-bottom: 28.13%;
}

#experience-title {
	text-align: center;
}

#job-list-row {
	display: flex;
	flex-direction: column;
}

#job-divider {
	width: 56.5%;
}

/* Moto G4 and Galaxy S5 */
@media (max-width: 361px) and (max-height: 641px) and (min-width: 360px) and (min-height: 640px) {
	/* Experience */
	#experience-container {
		padding-top: 21%;
		padding-bottom: 62%;
		padding-left: 7%;
		padding-right: 7%;
		display: flex;
		flex-direction: column;
	}
	#experience-title {
		text-align: center;
	}

	#experience-date-row {
		margin-bottom: 8%;
	}

	#experience-company,
	#experience-position,
	#experience-info {
		text-align: center;
		font-size: x-large;
	}

	#experience-text {
		font-size: x-large;
	}
}

