#contact {
	background-image: url('https://mdbootstrap.com/img/Photos/Horizontal/Nature/full page/img(11).jpg');
	background-size: cover;
}

#contact-container {
	background-color: #151e3f;
	opacity: 0.9;
	color: white;
	padding-top: 3.2%;
	padding-left: 7.2%;
	padding-right: 7.2%;
	padding-bottom: 19%;
}

#form-container {
	width: 65%;
}

#contact-row {
	display: flex;
	flex-direction: column;
	align-items: center;
}

#contact-title {
	text-align: center;
}

#contact-text-col {
	margin-bottom: 2%;
}

#contact-button-col {
	margin-bottom: 1%;
}

#contact-list {
	width: 100%;
}

.contact-object {
	width: 100%;
}

#contact-divider {
	border-color: #5b9bbc;
	width: 50%;
}

/* Moto G4 and Galaxy S5 */
@media (max-width: 361px) and (max-height: 641px) and (min-width: 360px) and (min-height: 640px) {
	#contact-container {
		padding-left: 7%;
		padding-right: 7%;
	}

	#contact-title {
		text-align: center;
	}

	#email-row {
		display: flex;
		flex-direction: column;
	}

	#form-container {
		align-self: center;
		width: 100%;
	}

	#name-input,
	#email-input,
	#message-input,
	#send-button {
		font-size: x-large;
	}

	#contact-text {
		font-size: large;
		/* text-align: center; */
	}

	#contact-anchor {
		background-color: red;
	}

	#button-row {
		display: flex;
		flex-direction: row;
		justify-content: center;
	}
}

